import styles from "./LogItem.module.css";

const LogItem = (props) => {
  return (
    <li className={styles["log__item"]}>
      <div className={styles["log__group"]}>
        <h4>Name</h4>
        <p>{props.name}</p>
      </div>
      <div className={styles["log__group"]}>
        <h4>Route</h4>
        <p>{props.route}</p>
      </div>
      <div className={styles["log__group"]}>
        <h4>Duration</h4>
        <p>{props.duration}</p>
      </div>
      <div className={styles["log__group"]}>
        <h4>Time</h4>
        <p>{props.time}</p>
      </div>
      <div className={styles["log__group"]}>
        <h4>Date</h4>
        <p>{props.date}</p>
      </div>
    </li>
  );
};

export default LogItem;
