import styles from "./Input.module.css";

const Input = (props) => {
  function inputChangeHandler(event) {
    props.onInputChange(event.target.value);
  }
  return (
    <input
      onChange={inputChangeHandler}
      className={`${styles.input} ${styles[props.size]}`}
      {...props}
    ></input>
  );
};

export default Input;
