import { useContext, useEffect } from "react";
import HorbContext from "../store/HorbContext";
import LogItem from "./LogItem";
import styles from "./LogList.module.css";

const LogList = () => {
  const HorbCtx = useContext(HorbContext);

  useEffect(() => {
    HorbCtx.getRouteList();
  }, []);

  let routeArray;

  if (HorbCtx.routeList) {
    let routeList = [];
    for (const log in HorbCtx.routeList) {
      routeList.push(HorbCtx.routeList[log]);
    }

    routeArray = routeList.map((ele) => {
      const processedDate = new Date(ele.time);
      return (
        <LogItem
          name={ele.name}
          route={ele.route}
          duration={ele.duration}
          time={`${processedDate.getHours()}:${processedDate.getMinutes()}`}
          date={`${processedDate.getDate()}/${
            processedDate.getMonth() + 1
          }/${processedDate.getFullYear()}`}
        />
      );
    });
  }

  return <ul className={styles.list}>{routeArray}</ul>;
};

export default LogList;
