import HorbContext from "./HorbContext";
import { useState } from "react";

const HorbContextProvider = (props) => {
  const [routeList, setRouteList] = useState({});
  async function sendRouteData(inputData) {
    const response = await fetch(
      "https://horb-32823-default-rtdb.asia-southeast1.firebasedatabase.app/route.json",
      {
        method: "POST",
        body: JSON.stringify(inputData),
        headers: {
          "Content-Type": "application/json",
        },
      }
    );
    const data = await response.json();
    console.log(data);
  }

  async function getRouteList(inputData) {
    const response = await fetch(
      "https://horb-32823-default-rtdb.asia-southeast1.firebasedatabase.app/route.json"
    );
    const data = await response.json();
    console.log(data);
    await setRouteList(data);
  }

  return (
    <HorbContext.Provider value={{ sendRouteData, getRouteList, routeList }}>
      {props.children}
    </HorbContext.Provider>
  );
};

export default HorbContextProvider;
