import Input from "../UI/Input";
import Selector from "../UI/Selector";
import styles from "./Form.module.css";
import { useState, useContext } from "react";
import HorbContext from "../store/HorbContext";

const Form = () => {
  const HorbCtx = useContext(HorbContext);
  const [namevalue, setNamevalue] = useState("");
  const [routevalue, setRoutevalue] = useState("");
  const [minvalue, setMinvalue] = useState(null);
  const [secvalue, setSecvalue] = useState(null);

  console.log(namevalue, routevalue, minvalue, secvalue);

  async function onFormSubmitHandler(event) {
    event.preventDefault();
    await HorbCtx.sendRouteData({
      name: namevalue,
      route: routevalue,
      duration: `${minvalue}:${secvalue}`,
      time: Date.now(),
    });
    await HorbCtx.getRouteList();
  }

  return (
    <div className={styles["form__wrapper"]}>
      <form onSubmit={onFormSubmitHandler} className={styles.form}>
        <Selector
          onSelectorHandler={setNamevalue}
          name="name"
          id="name-input"
          label="Name"
          options={["Yi Hein", "Yi Xuen", "Yi Kang"]}
        ></Selector>
        <Selector
          onSelectorHandler={setRoutevalue}
          name="Route"
          id="route-input"
          label="Route"
          options={["HillView", "Beauty World"]}
        ></Selector>
        <div className={styles.time}>
          <label htmlFor="min">
            <h3>Time</h3>
          </label>
          <Input
            onInputChange={setMinvalue}
            type="number"
            id="min"
            placeholder="min"
            size="small"
          />
          <Input
            onInputChange={setSecvalue}
            type="number"
            id="sec"
            placeholder="sec"
            size="small"
          />
        </div>
        <button className={styles.submit} type="submit">
          Submit
        </button>
      </form>
    </div>
  );
};

export default Form;
